section {
  margin: 50px auto 0;
  width: 50%;
}
h1 {
  font-weight: 500;
  letter-spacing: 1px;
  font-size: 5vh;
  color: #E9E0E0;
  margin: 0;
  padding: 0;
  margin-bottom: 5px;
  font-family: 'Helvetica Neue','Helvetica','Arial','sans-serif';
  text-shadow: 0 0 5px rgba(0,0,0,0.5);
}

form {
  margin-bottom: 40px;
  position: relative;
}
form:after {
  content: "";
  display: table;
  clear: both;
}
input {
  font-family: "Helvetica Neue", "Helvetica", "Arial", "sans-serif";
  font-size: 1.25rem;
  padding: 10px;
  border-radius: 3px 0 0 3px;
  border: 0;
  background: #fff;
  width: calc(100% - 72px);
  float: left;
  -webkit-box-shadow: inset 0 0 10px rgba(0, 0, 0, .3);
          box-shadow: inset 0 0 10px rgba(0, 0, 0, .3);
  border: 1px solid #fff;
  border-right: 0;
  -webkit-transition: border .2s ease-out;
  -o-transition: border .2s ease-out;
  transition: border .2s ease-out;
}
input:focus {
  border-color: #FF5050;
  outline: 0;
  -webkit-transition: border .3s ease-in;
  -o-transition: border .3s ease-in;
  transition: border .3s ease-in;
}
button {
  float: left;
  border: solid;
  width: 50px;
  font-size: 1.25rem;
  background:transparent;
  border: 1px solid #FF5050;
  border-left: 0;
  height: 50px;
  display: block;
  border-radius: 0 3px 3px 0;
  cursor: pointer;
  color: #fff;
  text-shadow: 0px -1px 0px rgba(0, 0, 0, .25);
  -webkit-box-shadow: inset 0 0 10px rgba(0, 0, 0, .3);
          box-shadow: inset 0 0 10px rgba(0, 0, 0, .3);
  -webkit-transition: background .2s ease-out;
  -o-transition: background .2s ease-out;
  transition: background .2s ease-out;
}
button:hover {
  background: #E71D32;
  -webkit-transition: background .3s ease-in;
  -o-transition: background .3s ease-in;
  transition: background .3s ease-in;
  border-color: #E71D32;
}
.tags {
  padding: 0;
  list-style-type: none;
  text-align: center;
  width: 90%;
  margin: 0 auto;
}
.tags li {
  background: #FF5050;
  display: inline-block;
  padding: 10px;
  border-radius: 3px;
  color: #fff;
  text-shadow: 0px -1px 0px rgba(0, 0, 0, .25);
  -webkit-box-shadow: inset 0 0 10px rgba(0, 0, 0, .3);
          box-shadow: inset 0 0 10px rgba(0, 0, 0, .3);
  border: 1px solid #FF5050;
  cursor: pointer;
  -webkit-transition: background .2s ease-out;
  -o-transition: background .2s ease-out;
  transition: background .2s ease-out;
  margin: 5px;
}
.tags li:hover {
  background: #E71D32;
  -webkit-transition: background .3s ease-in;
  -o-transition: background .3s ease-in;
  transition: background .3s ease-in;
  border-color: #E71D32;
}
.tags li i {
  margin-left: 5px;
}
.suggestions {
  background: #fff;
  border-radius: 3px;
  position: absolute;
  top: 56px;
  left: 0;
  padding: 10px;
  display: none;
  width: calc(100% - 20px);
  margin: 0;
  list-style-type: none;
}
.suggestions li {
  padding: 10px;
  border-bottom: 1px solid #DFE9E9;
  cursor: pointer;
  -webkit-transition: background .2s ease-out, text-shadow .2s ease-out, color .2s ease-out;
  -o-transition: background .2s ease-out, text-shadow .2s ease-out, color .2s ease-out;
  transition: background .2s ease-out, text-shadow .2s ease-out, color .2s ease-out;
}
.suggestions li:not(.title):hover {
  background: #FF5050;
  color: #fff;
  text-shadow: 0px -1px 0px rgba(0, 0, 0, .25);
  -webkit-transition: background .3s ease-in, text-shadow .3s ease-in, color .3s ease-in;
  -o-transition: background .3s ease-in, text-shadow .3s ease-in, color .3s ease-in;
  transition: background .3s ease-in, text-shadow .3s ease-in, color .3s ease-in;
}
.suggestions li:first-of-type {
  padding-top: 10px !important;
}
.suggestions li:last-of-type {
  border-bottom: 0;
}
.suggestions li.title {
  color: #AEB8B8;
  font-style: italic;
  padding-top: 30px;
}
.suggestions.active {
  display: block;
}
/* FOOTER */
.footer {
  position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    color: white;
    text-align: center;
}
/* HOMEPAGE BUTTON */
.nav-button {
  position: center;
  border: none;
  font-size: 28px;
  color: rgb(255, 255, 255);
  padding: 20px;
  width: 300px;
  text-align: center;
  -webkit-transition-duration: 0.8s; /* Safari */
  -o-transition-duration: 0.8s;
     transition-duration: 0.8s;
  text-decoration: none;
  overflow: hidden;
  cursor: pointer;
}
.nav-button:after {
  content: "";
  background: rgb(148, 128, 26);
  display: block;
  position: absolute;
  padding-top: 300%;
  padding-left: 350%;
  margin-left: -20px!important;
  margin-top: -120%;
  opacity: 0;
  -webkit-transition: all 0.8s;
  -o-transition: all 0.8s;
  transition: all 0.8s
}
.nav-button:active:after {
  padding: 0;
  margin: 0;
  opacity: 1;
  -webkit-transition: 0s;
  -o-transition: 0s;
  transition: 0s
}
/* PLAYLIST SEARCH INPUT */

form {
  outline: 0;
  /* float: left; */
  border-radius: 4px;
  padding-left: 30%;
  border:0;
}

form > .textbox {
  outline: 0;
  height: 42px;
  width: 244px;
  line-height: 42px;
  padding: 0 16px;
  background-color: rgba(255, 255, 255, 0.8);
  color: #212121;
  border: 0;
  float: left;
  border-radius: 4px 0 0 4px;
}

form > .textbox:focus {
  outline: 0;
  background-color: #FFF;
}

form > .button {
  outline: 0;
  background: none;
  background-color: rgba(38, 50, 56, 0.8);
  float: left;
  height: 42px;
  width: 42px;
  text-align: center;
  line-height: 42px;
  border: 0;
  color: #FFF;
  font: normal normal normal 14px/1 FontAwesome;
  font-size: 16px;
  text-rendering: auto;
  text-shadow: 0 1px 1px rgba(0, 0, 0, 0.2);
  -webkit-transition: background-color .4s ease;
  -o-transition: background-color .4s ease;
  transition: background-color .4s ease;
  border-radius: 0 4px 4px 0;
}

form > .button:hover {
  background-color: rgba(0, 150, 136, 0.8);
}

h3 {
  font-size: 5vw;
  font-family: 'AirbagFree-Regular';
  font-variant: small-caps;
}


